import React from "react"
import { alpha, useTheme } from "@mui/system"
import {
  Box,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from "@rp/react/web/mui/core"
import { ChevronRight } from "@rp/react/web/mui/icons"
import { FinanceDataTabs } from "../../../hooks/useWatchlistsFinanceDataTabNavigation"
import { watchlistDataTables } from "../../tables"

type CategorySidebarProps = {
  availableCategories: Array<FinanceDataTabs>
  selectedCategory: FinanceDataTabs
  onCategorySelect: (category: FinanceDataTabs) => void
}

function CategorySidebar({
  availableCategories,
  selectedCategory,
  onCategorySelect,
}: CategorySidebarProps) {
  const theme = useTheme()
  return (
    <Box
      sx={{
        width: 280,
        borderRight: 1,
        borderColor: "divider",
        overflow: "auto",
      }}
    >
      <List dense sx={{ p: 0 }}>
        {availableCategories.map(category => {
          const table = watchlistDataTables[category]
          return (
            <ListItem key={category} disablePadding>
              <ListItemButton
                selected={selectedCategory === category}
                onClick={() => onCategorySelect(category)}
                sx={{
                  px: 2,
                  py: 1,
                  mr: 1,
                  borderRadius: 2,
                  color: "text.primary",
                  "&:hover": {
                    backgroundColor: "elevation.dp08",
                    color: "text.primary",
                  },
                  "&.Mui-selected": {
                    backgroundColor: alpha(theme.palette.primary.dark, 0.2),
                    color: "primary.main",
                    "& .MuiListItemIcon-root": {
                      color: "primary.main",
                    },
                    "& .MuiListItemText-primary": {
                      color: "primary.main",
                      fontWeight: 400,
                    },
                  },
                  "& .MuiListItemIcon-root": {
                    color: "inherit",
                  },
                }}
              >
                <ListItemIcon sx={{ minWidth: 32 }}>{table.tableIcon}</ListItemIcon>
                <ListItemText
                  primary={table.tableLabel}
                  slotProps={{
                    primary: { fontWeight: 400, fontSize: 16 },
                  }}
                />
                <ChevronRight sx={{ fontSize: 18 }} />
              </ListItemButton>
            </ListItem>
          )
        })}
      </List>
    </Box>
  )
}

export { CategorySidebar }
